// todo ---------------------------- Style -------------------------------------
.wrapper {
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	height: 100%;
	max-width: $maxWidth;
}

[class*='_container'] {
	max-width: $maxWidthContainer;
	box-sizing: content-box;
	margin: 0 auto;
	padding: 0 16px;

	@include screen-375 {
		padding: 0 13px;
	}
}

[class*=' _img'] {
	display: inline-block;
	width: auto;
	height: auto;
	img {
		width: auto;
		height: auto;
		vertical-align: middle;
		object-fit: cover;
	}
}

[class*='_ibg'] {
	position: absolute;

	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	svg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

[class*='icon-'] {
	cursor: pointer;
	display: inline-flex;
	align-items: center;
	justify-content: center;
}
